.cat-info {
  display: flex;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px dotted #000;
  border-radius: 50%;
  animation: rotation 2.5s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

